*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
}
body {
  direction: rtl;
  font-family: 'vazir' !important;
  background-color: #f8f8f8;
}

nav{
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(117,117,117,0.3);
  height: 65px;
  padding: 0 20px;
  position: relative;
}


.container{
  width: 1400px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto  ;
}


.nav-logo{
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.nav-logo img{
  width: 75px;
}

.nav-logo .nav-toggle{
  background-color: transparent;
  border: none;
  width: 40px;
  height: 40px;
  font-size: 27px;
  cursor: pointer;
  display: none;
}

.nav-links .list{
  display: flex;
  align-items: center;
  /* height: 100%; */
}

.nav-links .list li{
  margin: 0 10px;
  
}

.nav-links .list li{
  color: #333;
  font-size: 15px;
}

.nav-social{
  display: flex;
  align-items: center;
}

.nav-social li{
  margin-left: 10px;
}

.nav-social li a{
  color: #333;
}

@media screen and (min-width:801px) {
    .nav-links{
      height: auto !important;
    }
}


@media screen and ( max-width:800px ){
  .nav-logo{
    width: 100%;
  }

  .nav-toggle{
    display: block !important;
  }

  .nav-links{
    position: absolute;
    right: 0;
    left: 0;
    top: 65px;
    height: 0;
    border-bottom: 1px solid rgb(245,245,245);
    background-color: #fff;
    overflow: hidden;
  }

  .nav-links{
    transition: all 0.3s ease;
  }

  .nav-links .list{
    display: block;
  }

  .nav-links .list li{
    padding: 15px 20px;
  }

  .nav-social{
    display: none;
  }
}